.container {
    width: 100%;
    font-family: 'dohyeon', sans-serif;
    position: relative;
}


.head-title {
    font-weight: 600;
    font-size: 32px;
    padding: 3vw;
    margin-bottom: 3vh;
}

.menu-title {
    font-weight: 400;
    font-size: 22px;
    padding: 3vw;
    margin-bottom: 2vh;
    box-shadow: 3px 3px 3px 3px lightgrey;
    border-radius: 1%;
}

img {
  border-radius: 20%;
  width: 26vw;
}

.menu-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    padding: 3vw;
}

.menu-info {
    display: flex;
    flex-direction: column;
}

.menu-name {
    font-size: 20px;
    font-weight: 600;
}

.menu-desc {
    color: gray;
}

.menu-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 8vh;
}

.jooryu-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 90%;
    padding: 3vw;
}

.jooryu-name {
    font-size: 15px;
    font-weight: 600;
}

.bottom-ending {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

@font-face {
    font-family: "dohyeon";
    src: url("./font/BMDOHYEON_ttf.ttf");
}